@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
:root{
  --left-color: rgb(255, 64, 255);
  --right-color: rgb(0, 89, 255);
  --box-shadow-color: rgba(97, 97, 97, 0.377);
}

body {
  margin: 0;
  font-family: 'Bebas Neue', cursive, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-image: linear-gradient(to right, var(--left-color), var(--right-color));
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.game-option{
    background-color: rgb(117, 117, 117);
    border: 5px white solid;
    width: 20vw;
    height: 20vw;
    border-radius: 50%;
    position: relative;
    box-shadow:  5px 5px 5px var(--box-shadow-color);
}

#computer > .game-option:active {
    -webkit-animation: play-computer 2s;
            animation: play-computer 2s;
}

#player > .game-option:active {
    -webkit-animation: play-player 2s;
            animation: play-player 2s;
}

#player > .game-option {
    transform: scaleX(-1);
}

.option-rock, .option-paper, .option-scissors {
    width: 50%;
    height: 50%;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    right: 65%;
    transform: translate(50%, -50%);
    background-color: white !important;
    box-shadow: 5px 5px 5px var(--box-shadow-color);
}

.option-rock::before, .option-rock::after, .option-paper::before, .option-paper::after, .option-scissors::before, .option-scissors::after {
    background-color: inherit;
}

.option-rock{
    background-color: red;
}

.option-rock::before {
    content: "";
    height: 100%;
    width: 70%;
    position: absolute;
    border-top-right-radius: 25%;
    border-bottom-right-radius: 25%;
}

.option-rock::after {
    content: "";
    height: 50%;
    width: 50%;
    position: absolute;
    top: -10%;
    right: 10%;
    border-radius: 50%;
}

.option-paper{
    background-color: green;
}

.option-paper::before {
    content: "";
    height: 100%;
    width: 100%;
    position: absolute;
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
}

.option-paper::after {
    content: "";
    height: 30%;
    width: 75%;
    position: absolute;
    top: -10%;
    right: 10%;
    transform: rotate(-40deg);
    border-top-right-radius: 20%;
    border-bottom-right-radius: 20%;
}

.option-scissors{
    background-color: yellow;
}

.option-scissors::before{
    content: "";
    height: 25%;
    width: 100%;
    position: absolute;
    top: 5%;
    transform: rotate(-15deg);
    border-top-right-radius: 20%;
    border-bottom-right-radius: 20%;
}

.option-scissors::after{
    content: "";
    height: 25%;
    width: 100%;
    position: absolute;
    top: 45%;
    transform: rotate(15deg);
    border-top-right-radius: 20%;
    border-bottom-right-radius: 20%;
}

@-webkit-keyframes play-player {
    0%{
        top: 0;
        right: 0;
    }
    65%{
        top: -10%;
        right: 0;
    }
    100%{
        top: 0;
        right: 120%;
    }
}

@keyframes play-player {
    0%{
        top: 0;
        right: 0;
    }
    65%{
        top: -10%;
        right: 0;
    }
    100%{
        top: 0;
        right: 120%;
    }
}

@-webkit-keyframes play-computer {
    0%{
        top: 0;
        left: 0;
    }
    65%{
        top: -10%;
        left: 0;
    }
    100%{
        top: 0;
        left: 120%;
    }
}

@keyframes play-computer {
    0%{
        top: 0;
        left: 0;
    }
    65%{
        top: -10%;
        left: 0;
    }
    100%{
        top: 0;
        left: 120%;
    }
}

#gameboard {
    display: flex;
    justify-content: space-between;
    position: relative;
}

#play-result{
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);
    color: white;
    display: none;
}

#play-result span {
    display: block;
}

#play-result-status {
    font-size: 30px;
}

#play-result-result {
    font-size: 20px;
}
#scoreboard {
    display: flex;
    justify-content: space-between;
    position: relative;
}

#scoreboard::before {
    content: "VS";
    color: white;
    display: inline-block;
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    font-size: 5em;
}

#computer-score, #player-score{
    background-color: white;
    width: 20%;
    height: 100px;
    min-width: 120px;
    box-shadow:  5px 5px 5px var(--box-shadow-color);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3em;
}

#computer-score {
    border-bottom-right-radius: 35%;
    box-shadow:  5px 5px 5px var(--box-shadow-color);
}

#computer-icon{
    background-color: gray;
    width: 35px;
    height: 35px;
    position: relative;
    margin-right: 20px;
}

#computer-icon::before{
    content: "";
    width: 75%;
    height: 75%;
    background-color: black;
    position: absolute;
    right: 50%;
    top: 50%;
    transform: translate(50%, -50%);
}

#computer-icon::after {
    content: "";
    width: 50%;
    height: 90%;
    background-color: gray;
    position: absolute;
    right: 50%;
    top: 50%;
    transform: translate(-110%, -50%);
}

#player-score {
    border-bottom-left-radius: 35%;
    box-shadow:  -5px 5px 5px var(--box-shadow-color);
}

#player-icon{
    background-color: gray;
    width: 30px;
    height: 30px;
    position: relative;
    border-top-right-radius: 50%;
    border-top-left-radius: 50%;
    margin-left: 20px;
}

#player-icon::before{
    content: "";
    width: 60%;
    height: 60%;
    background-color: gray;
    position: absolute;
    right: 50%;
    top: 50%;
    transform: translate(50%, -155%);
    border-radius: 50%;
}


.option-button {
    padding: 50px;
    padding: 50px;
    display: inline-block;
    background-color: red;
    color: white;
    margin: 0 auto;
    border-radius: 25%;
    box-sizing: border-box;
    position: relative;
    transform: scaleX(-1);
    cursor: pointer;
    margin: 0 20px;
    box-shadow: 5px 5px 5px var(--box-shadow-color);
}

.option-button:hover::before{
    -webkit-animation: button-label-fly 0.25s;
            animation: button-label-fly 0.25s;
}

.option-button:hover {
    background-color: rgb(235, 0, 0);
}

.option-button:active {
    transform: scale(-0.99, 0.99);
}

#option-button-rock:hover::before{
    content: "Rock";
    position: absolute;
    color: white;
    font-size: 20px;
    top: -25%;
    right: 50%;
    transform: scaleX(-1) translateX(-50%);
}

#option-button-paper:hover::before{
    content: "Paper";
    position: absolute;
    color: white;
    font-size: 20px;
    top: -25%;
    right: 50%;
    transform: scaleX(-1) translateX(-50%);
}

#option-button-scissors:hover::before{
    content: "Scissors";
    position: absolute;
    color: white;
    font-size: 20px;
    top: -25%;
    right: 50%;
    transform: scaleX(-1) translateX(-50%);
}

@-webkit-keyframes button-label-fly {
    from{
        top: 0;
        opacity: 0;
    }
    to{
        top: -25%;
        opacity: 1;
    }
}

@keyframes button-label-fly {
    from{
        top: 0;
        opacity: 0;
    }
    to{
        top: -25%;
        opacity: 1;
    }
}


